#page-detail {
  background-color: #FFF;
  padding: 30px;
}

#page-detail .container {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  padding-top: 32px;

  display: flex;
}

#page-detail .container main {
  flex: 1;
  margin-left: 32px;
}

#page-detail .container .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

#page-detail .container .header .social {
  display: flex;
  align-items: center;
}

#page-detail .header .social>button {
  margin-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;
  background-color: #f0f0f5;
  border: 0;
  border-radius: 50%;
  font-size: 24px;
  color: var(--text-color);
  transition: all .3s;
}

#page-detail .social>button.twitter:hover {
  background-color: #1DA1F2;
  color: #fff;
}

#page-detail .social>button.facebook:hover {
  background-color: #3B579D;
  color: #fff;
}

#page-detail .social>button.whatsapp:hover {
  background-color: #26CD64;
  color: #fff;
}

#page-detail .date {
  display: flex;
  align-items: center;
}

#page-detail .date span {
  margin-right: 8px;
  font-size: 16px;
}

#page-detail .date .calendar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #f0f0f5;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-color);
  transition: all .3s;
}

#page-detail .date .calendar svg {
  height: 24px;
  width: 24px;
}

#page-detail .date .calendar:hover {
  color: #fff;
  background-color: #455A64;
}

#page-detail .container .content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#page-detail .container .content h1 {
  font-size: 22px;
  text-align: justify;
  color: #444;
  padding: 16px 0;
  margin-bottom: 32px;

  border-top: 1px solid #f0f0f5;
  border-bottom: 1px solid #f0f0f5;
}

#page-detail .container .content .card-image {  
  padding: 8px;
  border-radius: 10px;
  margin-bottom: 16px;

  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f5;
}

#page-detail .container .content img {
  border-radius: 10px;
}

#page-detail .container .content .card-image span {
  color: var(--text-color);
  margin-top: 8px;
}

#page-detail .container .content p {
  font-size: 20px;
  margin-top: 16px;
  text-align: justify;
  color: #555;
  line-height: 28px;
}

#page-detail .container .content p::first-letter {
  margin-left: 40px;
}